import React,{useState} from 'react';
import db from '../db.js'
import t from '../components/t.js'
import { Divider,Row,Col,Image,Modal,Checkbox,Card} from 'antd';

import {EventList} from '../components/event.js'
import {SkillList} from '../components/skill.js'

// const CheckboxGroup = Checkbox.Group

// todo 
const cdnServer = 'https://cdn.jsdelivr.net/gh/wrrwrr111/pretty-derby/public/'

const PlayerCard = (props)=>{
  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = () => {
    if(props.onSelect){
      props.onSelect(props.data)
    }else{
      setIsModalVisible(true);
    }
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  return (
    <>
      <Card cover={
        <Image src={cdnServer+props.data.imgUrl} preview={false} onClick={showModal} width={'100%'}></Image>

      }>
        <Card.Meta title={t(props.data.name)} ></Card.Meta>
        <Card.Meta title={t(props.data.charaName)} ></Card.Meta>
      </Card>
      <Modal visible={isModalVisible} onOk={handleOk} onCancel={handleCancel} footer={null} width={800} >
        <div style={{height:144,display:'flex'}}>
          <Image src={cdnServer+props.data.imgUrl} width={128} height={128} resizemode={'cover'}></Image>
          <div style={{display:'flex',height:128,padding:24,flexDirection:'column',justifyContent:'space-between'}}>
            <div style={{fontSize:20,fontWeight:700}}>{t(props.data.charaName)}</div>
            <div style={{fontSize:20,fontWeight:700,color:'gray'}}>{props.data.charaName}</div>
            <div style={{fontSize:20,fontWeight:700,color:'gray'}}>{t(props.data.name)}</div>
            <div style={{fontSize:20,fontWeight:700,color:'gray'}}>{props.data.name}</div>
          </div>
        </div>
        <Divider></Divider>
        <AdaptBox2 player={props.data}></AdaptBox2>
        <Divider></Divider>
        <GrowBox2 player={props.data}></GrowBox2>
        <Divider></Divider>
        <SkillList skillList={props.data.skillList}></SkillList>
        <Divider></Divider>
        <EventList eventList={props.data.eventList} pid={props.data.id}></EventList>
      </Modal>
    </>
  )
}

const AdaptBox2 = (props)=>{

  const tableStyle = {
    width:"100%",
    cellPadding:4,
  }
  const cellStyle = {
    width:'20%',
    height:'32px',
    fontSize: 16,
    textAlign: 'flex-start',
    paddingLeft:16,
    fontWeight: 500,
    borderWidth:'thin',
    borderStyle:'none solid solid none',
    borderColor:'gray',
  }
  const headerCellStyle = {
    width:'20%',
    fontSize: 18,
    textAlign: 'center',
    fontWeight: 600,
    color:'#f5f5f5',
    textShadow: "0 2px #33333370",
    backgroundColor:'#32cd32C0',
    borderWidth:'thin',
    borderStyle:'none solid solid none',
    borderColor:'gray',
  }
  const adaptTextWrapperStyle = {paddingRight:16,display:'flex',width:'100%',alignItems:'center',justifyContent:'space-between'}

  const coloredGradeText = (text)=>{
    let color = 'gray';
    switch (text) {
      case 'S':
        color = '#FFD700';
        break
      case 'A':
        color = '#FFA500';
        break
      case 'B':
        color = '#BA55D3';
        break
      case 'C':
        color = '#90EE90';
        break
      case 'D':
        color = '#87CEEB';
        break
      default:
        color = 'gray'
    }
    return <div style={{fontSize:22,fontWeight:700,textShadow: "0 2px #33333370",color:color}}>{text}</div>
  }
  // {[`/\xa0`,coloredGradeText(props.player.grass)]}
  return(
    <div style={{borderRadius:'8px',borderStyle:'solid',borderWidth:'thin',borderColor:'gray'}}>
      <table {...tableStyle} >
        <tbody>
          <tr>
            <td style={{ ...headerCellStyle ,borderRadius:"8px 0 0 0",fontWeight:700,fontSize:18}}></td>
            <td style={{ ...cellStyle }}><div style={adaptTextWrapperStyle}>{[`/`,coloredGradeText(props.player.grass)]}</div></td>
            <td style={{ ...cellStyle }}><div style={adaptTextWrapperStyle}>{[`/`,coloredGradeText(props.player.dirt)]}</div></td>
            <td style={{ ...cellStyle }}>{`\xa0`}</td>
            <td style={{ ...cellStyle ,borderRadius:"0 8px 0 0",borderStyle:'none none solid none'}}>{`\xa0`}</td>
          </tr>
          <tr>
            <td style={{ ...headerCellStyle ,fontWeight:700,fontSize:18}}></td>
            <td style={{ ...cellStyle }}><div style={adaptTextWrapperStyle}>{[``,coloredGradeText(props.player.shortDistance)]}</div></td>
            <td style={{ ...cellStyle }}><div style={adaptTextWrapperStyle}>{[``,coloredGradeText(props.player.mile)]}</div></td>
            <td style={{ ...cellStyle }}><div style={adaptTextWrapperStyle}>{[``,coloredGradeText(props.player.mediumDistance)]}</div></td>
            <td style={{ ...cellStyle ,borderRadius:"0 0 8px 0",borderStyle:'none none solid none'}}><div style={adaptTextWrapperStyle}>{[``,coloredGradeText(props.player.longDistance)]}</div></td>
          </tr>
          <tr>
            <td style={{ ...headerCellStyle ,borderRadius:"0 0 0 8px",borderStyle:'none solid none none',fontWeight:700,fontSize:18}}></td>
            <td style={{ ...cellStyle ,borderStyle:'none solid none none'}}><div style={adaptTextWrapperStyle}>{[`/`,coloredGradeText(props.player.escape)]}</div></td>
            <td style={{ ...cellStyle ,borderStyle:'none solid none none'}}><div style={adaptTextWrapperStyle}>{[``,coloredGradeText(props.player.leading)]}</div></td>
            <td style={{ ...cellStyle ,borderStyle:'none solid none none'}}><div style={adaptTextWrapperStyle}>{[`/`,coloredGradeText(props.player.insert)]}</div></td>
            <td style={{ ...cellStyle ,borderRadius:"0 0 8px 0",borderStyle:'none none none none'}}><div style={adaptTextWrapperStyle}>{[`/`,coloredGradeText(props.player.tracking)]}</div></td>
          </tr>
        </tbody>
      </table>
    </div>
  )


}

const GrowBox2= (props)=>{
  const tableStyle = {
    width:"100%",
    cellPadding:4,
  }
  const headerCellStyle = {
    width:'20%',
    fontSize: 18,
    textAlign: 'center',
    fontWeight: 600,
    color:'#f5f5f5',
    textShadow: "0 2px #33333370",
    backgroundColor:'#32cd32C0',
    borderWidth:'thin',
    borderStyle:'none solid solid none',
    borderColor:'gray',
  }
  const cellStyle = {
    width:'20%',
    height:'40px',
    fontSize: 16,
    textAlign: 'center',
    fontWeight: 500,
    borderWidth:'thin',
    borderStyle:'none solid none none',
    borderColor:'gray',
  }

  return(
    <div style={{borderRadius:'8px',borderStyle:'solid',borderWidth:'thin',borderColor:'gray'}}>
      <table {...tableStyle} >
        <tbody>
          <tr>
            <th style={{ ...headerCellStyle ,borderRadius:"8px 0 0 0"}}>/</th>
            <th style={{ ...headerCellStyle }}>/</th>
            <th style={{ ...headerCellStyle }}>/</th>
            <th style={{ ...headerCellStyle }}>/</th>
            <th style={{ ...headerCellStyle ,borderRadius:"0 8px 0 0",borderStyle:'none none solid none'}}>/</th>
          </tr>
          <tr>
            <td style={{ ...cellStyle ,borderRadius:"0 0 0 8px"}}>{props.player.speedGrow}</td>
            <td style={{ ...cellStyle }}>{props.player.staminaGrow}</td>
            <td style={{ ...cellStyle }}>{props.player.powerGrow}</td>
            <td style={{ ...cellStyle }}>{props.player.gutsGrow}</td>
            <td style={{ ...cellStyle ,borderRadius:"0 0 8px 0",borderStyle:'none none none none'}}>{props.player.wisdomGrow}</td>
          </tr>
        </tbody>
      </table>
    </div>
  )

}


//todo filter
class Player extends React.Component{
  constructor(props){
    super(props)
    this.state = {list:props.playerList}
  }
  componentDidUpdate(prevProps){
    if(this.props.playerList !== prevProps.playerList){
      this.setState({list:this.props.playerList})
    }
  }
  render(){
    return(
      <Row justify="space-around">
        <Col span={22}>
          {
            ['3','2','1'].map(rare=>(
              <Row gutter={[16,16]} key={rare}>
                <Divider>{rare}</Divider>
                {this.state.list.filter(item=>item.rare===rare).map(player=>
                  <Col xxl={2} lg={3} sm={6} xs={6} key={player.id}>
                    <PlayerCard data={player} onSelect={this.props.onSelect}></PlayerCard>
                  </Col>)
                }
              </Row>
            ))
          }
        </Col>
      </Row>
    )
  }
}
Player.defaultProps={
  playerList:db.get('players').value()
}

export default Player
